"use client";

import { useEffect, useState } from "react";
import { Document, Page, pdfjs } from "react-pdf";
// Import CSS mới như bạn đã sửa (Đúng)
import "react-pdf/dist/Page/AnnotationLayer.css";
import "react-pdf/dist/Page/TextLayer.css";

// Cấu hình worker: Dùng cdnjs hoặc unpkg nhưng phải match version
// Cách an toàn nhất: Set workerSrc bên trong useEffect để đảm bảo chỉ chạy ở client
pdfjs.GlobalWorkerOptions.workerSrc = `//unpkg.com/pdfjs-dist@${pdfjs.version}/build/pdf.worker.min.js`;

interface Props {
  fileUrl: string;
  currPage: number;
  onLoadSuccess: (total: number) => void;
}

export default function PDFMainViewer({
  fileUrl,
  currPage,
  onLoadSuccess,
}: Props) {
  // State để đảm bảo chỉ render PDF sau khi mount (tránh lỗi DOMMatrix ở server)
  const [isClient, setIsClient] = useState(false);

  useEffect(() => {
    setIsClient(true);
  }, []);

  if (!isClient) {
    // Khi đang SSR hoặc chưa mount xong, hiển thị loading placeholder
    return (
      <div className="flex items-center justify-center h-[500px] bg-gray-100 border rounded-lg text-gray-400">
        Đang khởi tạo trình đọc PDF...
      </div>
    );
  }

  return (
    <div className="flex justify-center bg-gray-200 border rounded-lg overflow-auto h-full min-h-[500px] shadow-inner p-4">
      <Document
        file={fileUrl}
        onLoadSuccess={({ numPages }) => onLoadSuccess(numPages)}
        loading={
          <div className="flex items-center justify-center h-96 text-gray-500">
            <span className="animate-pulse">Đang tải tài liệu...</span>
          </div>
        }
        error={
          <div className="flex flex-col items-center justify-center h-96 text-red-500 gap-2">
            <p>⚠ Không thể tải file PDF.</p>
            <p className="text-sm text-gray-500">
              Có thể do đường dẫn không đúng hoặc lỗi mạng.
            </p>
          </div>
        }
        className="shadow-lg"
      >
        <Page
          pageNumber={currPage}
          // Scale: Responsive theo màn hình (hoặc fix cứng)
          scale={1.2}
          renderAnnotationLayer={false}
          renderTextLayer={false}
          className="bg-white"
        />
      </Document>
    </div>
  );
}
