"use client";

import { useEffect } from "react";
import { useForm, useFieldArray } from "react-hook-form";
import { Plus, Trash2, Save, Clock } from "lucide-react";
import { CheckpointData, SaveCheckpointRequest } from "@/types/presentation";

type CheckpointFormValues = {
  question: string;
  options: { id: string; text: string }[];
  correctAnswer: string; // Trong editor đơn giản này ta cho chọn 1 đáp án đúng
  timeLimit: number;
};

interface Props {
  pageNumber: number;
  existingCheckpoint?: CheckpointData; // Nếu trang này đã có câu hỏi
  onSave: (data: Omit<SaveCheckpointRequest, "userId">) => Promise<void>;
  isSaving: boolean;
}

export default function CheckpointEditorSidebar({
  pageNumber,
  existingCheckpoint,
  onSave,
  isSaving,
}: Props) {
  const { register, control, handleSubmit, reset } =
    useForm<CheckpointFormValues>({
      defaultValues: {
        question: "",
        options: [
          { id: "A", text: "" },
          { id: "B", text: "" },
        ],
        correctAnswer: "A",
        timeLimit: 30,
      },
    });

  const { fields, append, remove } = useFieldArray({
    control,
    name: "options",
  });

  // Khi chuyển trang, nếu trang đó có câu hỏi cũ thì fill vào form, không thì reset
  useEffect(() => {
    if (existingCheckpoint) {
      reset({
        question: existingCheckpoint.question,
        options: existingCheckpoint.options,
        correctAnswer: existingCheckpoint.correctAnswer[0] || "A", // Lấy đáp án đầu tiên
        timeLimit: existingCheckpoint.timeLimit,
      });
    } else {
      reset({
        question: "",
        options: [
          { id: "A", text: "" },
          { id: "B", text: "" },
          { id: "C", text: "" },
          { id: "D", text: "" },
        ],
        correctAnswer: "A",
        timeLimit: 30,
      });
    }
  }, [existingCheckpoint, pageNumber, reset]);

  const onSubmit = (data: CheckpointFormValues) => {
    // Convert dữ liệu form thành format API cần
    const payload = {
      pageNumber,
      question: data.question,
      options: data.options,
      correctAnswer: [data.correctAnswer], // API nhận mảng string[]
      timeLimit: Number(data.timeLimit),
    };
    onSave(payload);
  };

  // UI Helper: Tạo ID tự động (A, B, C...) cho option mới
  const addOption = () => {
    const nextId = String.fromCharCode(65 + fields.length); // 65 = 'A'
    append({ id: nextId, text: "" });
  };

  return (
    <div className="h-full flex flex-col bg-white border-l shadow-sm">
      {/* Header */}
      <div className="p-4 border-b bg-gray-50">
        <h3 className="font-bold text-gray-800 flex items-center gap-2">
          <span className="bg-blue-600 text-white text-xs px-2 py-1 rounded">
            Trang {pageNumber}
          </span>
          {existingCheckpoint ? "Chỉnh sửa câu hỏi" : "Thêm câu hỏi mới"}
        </h3>
      </div>

      {/* Form Content (Scrollable) */}
      <div className="flex-1 overflow-y-auto p-4 custom-scrollbar">
        <form
          id="checkpoint-form"
          onSubmit={handleSubmit(onSubmit)}
          className="space-y-5"
        >
          {/* Câu hỏi */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1">
              Nội dung câu hỏi
            </label>
            <textarea
              {...register("question", { required: true })}
              className="w-full border border-gray-300 rounded-md p-2 text-sm focus:ring-2 focus:ring-blue-500 outline-none min-h-20"
              placeholder="Nhập câu hỏi cho sinh viên..."
            />
          </div>

          {/* Danh sách đáp án */}
          <div>
            <div className="flex justify-between items-center mb-2">
              <label className="block text-sm font-medium text-gray-700">
                Các phương án
              </label>
              <button
                type="button"
                onClick={addOption}
                className="text-xs flex items-center gap-1 text-blue-600 hover:text-blue-700"
              >
                <Plus size={14} /> Thêm
              </button>
            </div>

            <div className="space-y-3">
              {fields.map((field, index) => (
                <div key={field.id} className="flex gap-2 items-start">
                  {/* Nút chọn đáp án đúng */}
                  <div className="pt-2">
                    <input
                      type="radio"
                      value={field.id}
                      {...register("correctAnswer")}
                      className="w-4 h-4 text-blue-600 cursor-pointer"
                      title="Chọn làm đáp án đúng"
                    />
                  </div>

                  {/* Input nội dung đáp án */}
                  <div className="flex-1">
                    <div className="flex items-center gap-2 mb-1">
                      <span className="text-xs font-bold text-gray-500 w-4">
                        {String.fromCharCode(65 + index)}.
                      </span>
                      <input
                        {...register(`options.${index}.text` as const, {
                          required: true,
                        })}
                        className="flex-1 border border-gray-300 rounded p-1.5 text-sm outline-none focus:border-blue-500"
                        placeholder={`Đáp án ${String.fromCharCode(
                          65 + index
                        )}`}
                      />
                    </div>
                  </div>

                  {/* Nút xoá (chỉ hiện nếu có > 2 option) */}
                  {fields.length > 2 && (
                    <button
                      type="button"
                      onClick={() => remove(index)}
                      className="pt-2 text-gray-400 hover:text-red-500"
                    >
                      <Trash2 size={16} />
                    </button>
                  )}
                </div>
              ))}
            </div>
            <p className="text-xs text-gray-500 mt-2 italic">
              * Tích vào ô tròn để chọn đáp án đúng.
            </p>
          </div>

          {/* Thời gian */}
          <div>
            <label className="block text-sm font-medium text-gray-700 mb-1 items-center gap-2">
              <Clock size={16} /> Thời gian trả lời (giây)
            </label>
            <input
              type="number"
              {...register("timeLimit", { min: 10, max: 300 })}
              className="w-full border border-gray-300 rounded-md p-2 text-sm focus:ring-2 focus:ring-blue-500 outline-none"
            />
          </div>
        </form>
      </div>

      {/* Footer Actions */}
      <div className="p-4 border-t bg-gray-50">
        <button
          form="checkpoint-form"
          type="submit"
          disabled={isSaving}
          className="w-full flex justify-center items-center gap-2 bg-blue-600 text-white py-2.5 rounded-md hover:bg-blue-700 font-medium transition disabled:opacity-50"
        >
          {isSaving ? (
            <span className="animate-spin h-4 w-4 border-2 border-white border-t-transparent rounded-full" />
          ) : (
            <Save size={18} />
          )}
          {existingCheckpoint ? "Lưu thay đổi" : "Lưu câu hỏi"}
        </button>
      </div>
    </div>
  );
}
